import * as React from "react";
import intl from "react-intl-universal";
import { parseJwt } from "HOC/withApiService";
import locales from "i18n";

const SessionContext = React.createContext();
export const SessionContextConsumer = SessionContext.Consumer;

let initialState = {
  i18nInitDone: false,
  spinner: 0,
  locale: localStorage.getItem("locale") || "en",
  roles: "",
  isAuthenticated: false,
  notifications: [],
  pageCode: "",
  jwt1: "",
  jwt2: ""
};

function compareRoles(rolesA, rolesB) {
  return rolesA.every(role => {
    return rolesB.includes(role);
  });
}

class SessionProvider extends React.Component {
  state = {
    ...initialState,
    _setState: next => {
      if (typeof next === "function") {
        this.setState(
          state => next(state),
          () => {
            console.log("-------new session state:", this.state);
          }
        );
      } else {
        this.setState(
          state => ({
            ...next
          }),
          () => {
            console.log("------new session state:", this.state);
          }
        );
      }
    },
    startSpinner: () => {
      this.setState(state => ({
        ...state,
        spinner: ++state.spinner
      }));
    },
    stopSpinner: () => {
      let spinner = this.state.spinner;
      let nextState = --spinner;
      if (nextState < 0) {
        nextState = 0;
      }
      this.setState(state => ({
        ...state,
        spinner: nextState
      }));
    },
    changeLocale: () => {
      const newLocale = this.state.locale === "en" ? "ar" : "en";
      intl
        .init({
          currentLocale: newLocale,
          locales
        })
        .then(() => {
          this.setState(
            state => ({
              ...state,
              i18nInitDone: true,
              locale: newLocale
            }),
            () => {
              initialState.i18nInitDone = true;
              initialState.locale = newLocale;
              localStorage.setItem("locale", newLocale);
            }
          );
        });
    },
    removeNotification: id => {
      this.setState(state => ({
        notifications: state.notifications.filter(n => n.id !== id)
      }));
    },
    addNotification: ({
      id = Math.random().toString(),
      timeOutInMillis = 3000,
      type = "is-warning",
      message = ""
    }) => {
      message = typeof message === "string" ? message : JSON.stringify(message);
      const newNotifications = [
        ...this.state.notifications,
        { id, type, message, timeOutInMillis }
      ];
      this.setState({
        notifications: newNotifications
      });
    },
    hasRoles: roles => {
      if (Array.isArray(roles)) {
        return compareRoles(roles, this.state.roles);
      } else if (typeof roles === "string") {
        return compareRoles(roles.split(" "), this.state.roles);
      } else {
        console.error("---- roles must be a string or array");
        return;
      }
    },
    signout: () => {
      sessionStorage.clear();
      this.setState({
        ...initialState
      });
    }
  };

  componentDidMount() {
    try {
      const jwt1 = sessionStorage.getItem("jwt1") || "";
      const jwt2 = sessionStorage.getItem("jwt2") || "";

      const userObject = jwt1 !== "" ? JSON.parse(parseJwt(jwt1).USER) : {};
      const xbcState = jwt2 !== "" ? JSON.parse(parseJwt(jwt2).XBC_STATE) : {};

      intl
        .init({
          currentLocale: localStorage.getItem("locale") || "en",
          locales
        })
        .then(() => {
          this.setState(
            state => ({
              ...state,
              i18nInitDone: true,
              jwt1,
              jwt2,
              ...userObject,
              ...xbcState
            }),
            () => {
              initialState.i18nInitDone = true;
            }
          );
        });
    } catch (e) {
      console.error("-----sessionContext didMount error:", e);
    }
  }

  render() {
    return (
      this.state.i18nInitDone && (
        <SessionContext.Provider value={this.state}>
          {this.props.children}
        </SessionContext.Provider>
      )
    );
  }
}

export default SessionProvider;



