import React from "react";

const elem = document.getElementById("xbcGatewayServicesEp");
const url = elem ? elem.getAttribute("di") : "/nic-xbc-gateway/xbc/gateway/";

export const urls = {
  login: "/api/login",
  postLogin: `${url}security/post-login`,

  crossing: {
    arrival: {
      search: {
        citizen: `${url}crossing/search/arrival/citizen`
      },
      update: {
        citizen: `${url}crossing/search/arrival/updateCitizenDetails`
      },
      complete: {
        citizen: `${url}crossing/arrival/complete/citizen`
      }
    },
    departure: {
      search: {
        citizen: `${url}crossing/search/departure/citizen`
      },
      update: {
        citizen: `${url}crossing/search/departure/updateCitizenDetails`
      },
      complete: {
        citizen: `${url}crossing/departure/complete/citizen`
      }
    },
    fetchTripInfo: `${url}crossing/search/tripInfo/fetch`,
    updateTripInfo: `${url}crossing/search/tripInfo/update`,
    searchCwl: `${url}crossing/cwl/search`,
    searchCwlDetails: `${url}crossing/cwl/details`
  }
};

export function makeCrossingUrlFor(action, travelerType, crossingType) {
  const { crossing } = urls;
  const result = [crossingType, action, travelerType].reduce((prev, curr) => {
    return prev[curr];
  }, crossing);
  console.log("------crossing url:", result);
  return result;
}

export function parseJwt(token) {
  var base64Url = token.split(".")[1];
  var base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
  return JSON.parse(window.atob(base64));
}

export function post({ url, data, method }) {
  const jwt2 = sessionStorage.getItem("jwt2") || "";
  return fetch(url, {
    body: JSON.stringify(data), // must match 'Content-Type' header
    headers: {
      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      Authorization: "Bearer " + jwt2
    },
    method: method || "POST", // *GET, POST, PUT, DELETE, etc.
    referrer: "no-referrer" // *client, no-referrer
  })
    .then(res => res.json().catch(err => res))
    .then(res => {
      if (res.status && res.status !== 200) {
        throw res;
      } else {
        return res;
      }
    })
    .catch(e => {
      throw e.message || e.statusText || "unknown post error";
    });
}

export function get(url) {
  return fetch(url)
    .then(res => res.json())
    .catch(e => {
      console.error("--------api get:", e);
      throw e.message || e.statusText || "unknown get error";
    });
}

export default function withApiService(WrappedComponent) {
  class C extends React.Component {
    render() {
      return (
        <WrappedComponent
          {...this.props}
          apiService={{ post, get, urls, parseJwt }}
        />
      );
    }
  }
  C.displayName = `withApiService(${WrappedComponent.displayName ||
    WrappedComponent.name})`;
  return C;
}



